.contact-us {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  padding-top: 4px;

  @media (max-width: $tablet-max-width) {
    padding-top: 8px;
  }

  @media (max-width: $mobile-max-width) {
    padding-top: 1px;
  }
}

.contact-us__mail,
.contact-us__tel {
  display: block;
  text-decoration: none;
  color: $basic-light;
  line-height: 18px;
  // padding-left: 3px;
  // padding-right: 3px;
}

.contact-us__tel {
  margin-left: 88px;

  @media (max-width: $tablet-max-width) {
    margin-left: 33px;
  }

  @media (max-width: $mobile-max-width) {
    margin-left: 21px;
  }
}

.contact-us__tel span,
.contact-us__mail span {
  position: relative;

  &:hover::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 50%;
    bottom: 0;
    right: -0.3em;
    background-color: rgba(lighten($basic-blue, 30%), 0.4);
  }

  &:active::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 50%;
    bottom: 0;
    right: -0.3em;
    background-color: rgba(saturate($basic-blue, 30%), 0.4); // rgba($basic-light, 0.4);
  }

  @media (max-width: $tablet-max-width) {
    display: none;
  }
}

.contact-us__icon-mail,
.contact-us__icon-phone {
  fill: $basic-blue;
}

.contact-us__icon-phone {
  position: relative;
  top: 4px;
  margin-right: 6px;

  @media (max-width: $tablet-max-width) {
    width: 24px;
    height: 24px;
    margin-right: 0;
  }
}

.contact-us__icon-mail {
  position: relative;
  top: 2px;
  margin-right: 10px;

  @media (max-width: $tablet-max-width) {
    width: 25.33px;
    height: 18.67px;
    margin-right: 0;
  }
}

.contact-us__callback {
  text-decoration: none;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid $basic-light;
  color: $basic-light;
  margin-left: 50px;
  padding-top: 13px;
  padding-left: 25px;
  padding-right: 27px;
  padding-bottom: 15px;

  &:hover {
    border-color: lighten($basic-blue, 30%);
    color: lighten($basic-blue, 30%);
  }

  &:active {
    border-color: darken($basic-blue, 20%);
    color: darken($basic-blue, 20%);
  }

  @media (max-width: $tablet-max-width) {
    display: none;
  }
}
